// redux/usersSlice.js
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { User,initialUsers } from '../model/UserModel';


export const fetchUsers = createAsyncThunk(
  'users/fetchUsers',
  async ({ page = 1, limit = 10 }, { getState }) => {
    console.log(page,"page number_______________________");
    const currentUsers = getState().users.users;

    const usersToReturn = currentUsers.length > 0 ? currentUsers : initialUsers;

    const startIndex = (page - 1) * limit;
    const endIndex = startIndex + limit;
    console.log(startIndex,endIndex,"index value =====================");
    console.log(usersToReturn.length, "length of usersToReturn");

    const paginatedUsers = usersToReturn.slice(startIndex, endIndex);

    console.log(paginatedUsers);

    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({
          statusCode: 200,
          data: paginatedUsers,
          total: usersToReturn.length,
          page,
          limit
        });
      }, 500);
    });
  }
);

export const addUser = createAsyncThunk(
  'users/addUser',
  async (userData) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        const newUser = new User(
          Date.now(), 
          userData.username, 
          userData.email, 
          userData.role
        );
        resolve({
          statusCode: 201,
          data: newUser
        });
      }, 500);
    });
  }
);

export const updateUser = createAsyncThunk(
  'users/updateUser',
  async (userData) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({
          statusCode: 200,
          data: userData
        });
      }, 500);
    });
  }
);

export const deleteUser = createAsyncThunk(
  'users/deleteUser',
  async (userId) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({
          statusCode: 200,
          data: { id: userId }
        });
      }, 500);
    });
  }
);

const usersSlice = createSlice({
  name: 'users',
  initialState: {
    users: initialUsers,
    loading: false,
    error: null,
    total: initialUsers.length
  },
  reducers: {
    clearError: (state) => {
      state.error = null;
    }
  },
  extraReducers: (builder) => {
    builder
      // Fetch Users
      .addCase(fetchUsers.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(fetchUsers.fulfilled, (state, action) => {
        state.users = action.payload.data;
        state.total = action.payload.total;
        state.loading = false;
      })
      .addCase(fetchUsers.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message || 'Failed to fetch users';
      })
      // Add User
      .addCase(addUser.fulfilled, (state, action) => {
        state.users.push(action.payload.data);
        state.error = null;
      })
      .addCase(addUser.rejected, (state, action) => {
        state.error = action.error.message || 'Failed to add user';
      })
      // Update User
      .addCase(updateUser.fulfilled, (state, action) => {
        const index = state.users.findIndex(
          user => user.id === action.payload.data.id
        );
        if (index !== -1) {
          state.users[index] = action.payload.data;
        }
        state.error = null;
      })
      .addCase(updateUser.rejected, (state, action) => {
        state.error = action.error.message || 'Failed to update user';
      })
      // Delete User
      .addCase(deleteUser.fulfilled, (state, action) => {
        state.users = state.users.filter(
          user => user.id !== action.payload.data.id
        );
        state.error = null;
      })
      .addCase(deleteUser.rejected, (state, action) => {
        state.error = action.error.message || 'Failed to delete user';
      });
  }
});

export const { clearError } = usersSlice.actions;

export default usersSlice.reducer;