import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { initialUsers } from '../model/UserModel';

export const loginUser = createAsyncThunk(
  'auth/loginUser',
  async (credentials, { rejectWithValue }) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        // Find user in the initial users array
        const user = initialUsers.find(
          u => u.email === credentials.username
        );

        // Check if user exists and has admin/super_admin role
        if (
          user && 
          (user.role === 'ADMIN' || user.role === 'SUPER_ADMIN') && 
          // You might want to add password check if you extend the user model
          credentials.password === 'admin123' // Placeholder password check
        ) {
          resolve({
            statusCode: 200,
            data: {
              user: {
                id: user.id,
                username: user.username,
                role: user.role
              },
            }
          });
        } else {
          reject(rejectWithValue({
            statusCode: 401,
            message: 'Only admin can log in'
          }));
        }
      }, 1000); // Simulate network delay
    });
  }
);

// Rest of the authSlice remains the same
const authSlice = createSlice({
  name: 'auth',
  initialState: {
    user: null,
    isAuthenticated: false,
    isLoading: false,
    error: null
  },
  reducers: {
    logout: (state) => {
      state.user = null;
      state.isAuthenticated = false;
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(loginUser.pending, (state) => {
        state.isLoading = true;
      })
      .addCase(loginUser.fulfilled, (state, action) => {
        state.isAuthenticated = true;
        state.user = action.payload.data.user;
        state.isLoading = false;
      })
      .addCase(loginUser.rejected, (state, action) => {
        state.isAuthenticated = false;
        state.error = action.payload;
        state.isLoading = false;
      });
  }
});

export const { logout } = authSlice.actions;
export default authSlice.reducer;