import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchUsers, deleteUser } from '../features/userSlice';
import { 
  Trash2Icon, 
  EditIcon 
} from 'lucide-react';
import EditUserModal from '../components/EditUserModal';
import { toast } from 'react-toastify';
import { Pagination } from '@mui/material';

const UserListing = () => {
  const dispatch = useDispatch();
  const { users, loading, total } = useSelector(state => state.users);
  const { user: loggedInUser } = useSelector(state => state.auth);
  const theme = useSelector((state) => state.theme.mode);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  
  // State for modal
  const [selectedUser, setSelectedUser] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    dispatch(fetchUsers({ page}));
  }, [dispatch, page]);

  const handleDelete = (userId) => {
    if (window.confirm('Are you sure you want to delete this user?')) {
      dispatch(deleteUser(userId));
      toast.success(`Deleted successfully!`, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      });
    }
  };

  // Open edit modal
  const handleEdit = (user) => {
    setSelectedUser(user);
    setIsModalOpen(true);
  };

  // Close modal
  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedUser(null);
  };


  const filteredUsers = users.filter(user => 
    user.id !== loggedInUser?.id && 
    user.role !== 'SUPER_ADMIN' && 
    (
      user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
      user.email.toLowerCase().includes(searchTerm.toLowerCase())
    )
  );

  // Handle page change
  const handlePageChange = (event, value) => {
    setPage(value);
  };

  return (
    <div className={`${theme} bg-white dark:bg-gray-900 shadow-md rounded-lg`}>
      {/* Search Input */}
      <div className="p-4 border-b dark:border-gray-700">
        <input 
          type="text"
          placeholder="Search users..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full p-2 border rounded dark:bg-gray-800 dark:text-white dark:border-gray-600"
        />
      </div>

      {/* User Table */}
      <div className="overflow-x-auto">
        <table className="w-full">
          <thead className="bg-gray-100 dark:bg-gray-800">
            <tr>
              <th className="p-3 text-left text-gray-600 dark:text-gray-300">Username</th>
              <th className="p-3 text-left hidden md:table-cell text-gray-600 dark:text-gray-300">Email</th>
              <th className="p-3 text-left hidden sm:table-cell text-gray-600 dark:text-gray-300">Role</th>
              <th className="p-3 text-right text-gray-600 dark:text-gray-300">Actions</th>
            </tr>
          </thead>
          <tbody>
            {loading ? (
              <tr>
                <td colSpan="4" className="text-center p-4 dark:text-white">
                  Loading...
                </td>
              </tr>
            ) : filteredUsers.length === 0 ? (
              <tr>
                <td colSpan="4" className="text-center p-4 dark:text-white">
                  No users found
                </td>
              </tr>
            ) : (
              filteredUsers.map(user => (
                <tr 
                  key={user.id} 
                  className="border-b dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700 transition"
                >
                  <td className="p-3 text-gray-800 dark:text-white">{user.username}</td>
                  <td className="p-3 hidden md:table-cell text-gray-800 dark:text-white">{user.email}</td>
                  <td className="p-3 hidden sm:table-cell text-gray-800 dark:text-white">{user.role}</td>
                  <td className="p-3 text-right">
                    <div className="flex justify-end space-x-2">
                      <button 
                        onClick={() => handleEdit(user)}
                        className="text-yellow-500 hover:text-yellow-600 dark:text-yellow-400 dark:hover:text-yellow-500"
                      >
                        <EditIcon className="h-5 w-5" />
                      </button>
                      <button 
                        onClick={() => handleDelete(user.id)}
                        className="text-red-500 hover:text-red-600 dark:text-red-400 dark:hover:text-red-500"
                      >
                        <Trash2Icon className="h-5 w-5" />
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* Pagination */}
      <div className="p-4 flex justify-center items-center dark:text-white">
        <Pagination 
          count={Math.ceil(total / 10)}
          page={page}
          onChange={handlePageChange}
          color="primary"
        />
      </div>

      {/* Edit User Modal */}
      <EditUserModal 
        user={selectedUser}
        isOpen={isModalOpen}
        onClose={handleCloseModal}
      />
    </div>
  );
};

export default UserListing;