import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import {
  MenuIcon,
  UsersIcon,
  UserPlusIcon,
  ChevronDownIcon,
  LogOutIcon
} from 'lucide-react';
import { toggleTheme } from '../features/themeSlice';
import { logout } from '../features/authSlice';
import UserListing from './UserListing';
import AddUser from './AddUser';

const Dashboard = () => {
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const [activeTab, setActiveTab] = useState('list');
  const dispatch = useDispatch();
  const navigate = useNavigate();

  // Get the current theme and user from Redux
  const theme = useSelector((state) => state.theme.mode);
  const { user } = useSelector((state) => state.auth);

  const handleLogout = () => {
    dispatch(logout());
    navigate('/login');
  };

  const handleThemeToggle = () => {
    dispatch(toggleTheme());
  };

  return (
    <div className={`${theme} flex h-screen`}>
      {/* Sidebar */}
      <div
        className={`
          ${isSidebarOpen ? 'w-64' : 'w-20'}
          bg-gray-800 text-white 
          transition-all duration-300 
          overflow-hidden
        `}
      >
        <div className="flex items-center justify-between p-4">
          <h2
            className={`
              ${isSidebarOpen ? 'block' : 'hidden'} 
              text-md font-bold
            `}
          >
            RBAC Task
          </h2>
          <button
            onClick={() => setIsSidebarOpen(!isSidebarOpen)}
            className="focus:outline-none"
          >
            <MenuIcon className="h-6 w-6" />
          </button>
        </div>

        <nav className="mt-10">
          <button
            onClick={() => setActiveTab('list')}
            className={`
              w-full flex items-center p-4 
              ${activeTab === 'list' ? 'bg-gray-700' : 'hover:bg-gray-700'}
            `}
          >
            <UsersIcon className="h-5 w-5 mr-3" />
            <span className={isSidebarOpen ? 'block' : 'hidden'}>User List</span>
          </button>

          <button
            onClick={() => setActiveTab('add')}
            className={`
              w-full flex items-center p-4 
              ${activeTab === 'add' ? 'bg-gray-700' : 'hover:bg-gray-700'}
            `}
          >
            <UserPlusIcon className="h-5 w-5 mr-3" />
            <span className={isSidebarOpen ? 'block' : 'hidden'}>Add User</span>
          </button>
        </nav>
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-y-auto bg-gray-100 dark:bg-gray-900">
        {/* Navbar */}
        <header className="bg-white dark:bg-gray-800 shadow p-4 flex justify-between items-center">
          <h1 className="text-xl font-bold text-gray-800 dark:text-gray-100">
            {activeTab === 'list' ? 'User List' : 'Add User'}
          </h1>

          <div className='flex'>
            {/* Theme Toggle */}
            <button
              onClick={handleThemeToggle}
              className="p-2 mr-2 bg-gray-200 dark:bg-gray-700 rounded-full hover:bg-gray-300 dark:hover:bg-gray-600 transition"
            >
              {theme === 'dark' ? '' : ''}
            </button>


            {/* User Dropdown */}
            <div className="relative group">
              <button className="flex items-center space-x-2 hover:bg-gray-100 dark:hover:bg-gray-700 p-2 rounded-md">
                <span className="font-medium text-gray-800 dark:text-gray-100">
                  {user?.username}
                </span>
                <ChevronDownIcon className="h-4 w-4 text-gray-800 dark:text-gray-100" />
              </button>

              <div
                className="
              absolute right-0 mt-2 w-48 bg-white dark:bg-gray-800 rounded-md shadow-lg 
              invisible group-hover:visible 
              opacity-0 group-hover:opacity-100 
              transition-all duration-300 
              z-20
            "
              >
                <button
                  onClick={handleLogout}
                  className="w-full text-left px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-700 flex items-center text-gray-800 dark:text-gray-100"
                >
                  <LogOutIcon className="h-4 w-4 mr-2" />
                  Logout
                </button>
              </div>
            </div>
          </div>

        </header>

        <main className="p-6">
          {activeTab === 'list' ? <UserListing /> : <AddUser />}
        </main>
      </div>
    </div>
  );
};

export default Dashboard;
